import { request } from "../utils"

//放置和用户相关的请求
//登录
export function LoginApi(formData) {
    return request({
        url: "/authorizations",
        method: "POST",
        data: formData
    })
}

//获取用户信息
export function getProfileApi() {
    return request({
        url: "/user/profile",
        method: "GET",
    })
}