import { createSlice } from "@reduxjs/toolkit";
import { removeToken, request } from "../../utils/index"
import {setToken as _setToken,getToken} from "../../utils"
import { getProfileApi,LoginApi } from "../../apis/user";

//用户相关的状态管理
const userStore = createSlice({
    name: "user",
    //数据状态,初始状态
    initialState: {
        // token: localStorage.getItem("token_Key") || ""
        token: getToken() || "",
        //用户信息,右上角
        userInfo:{}
    },
    //同步修改方法
    reducers: {
        setToken(state, action) {
            state.token = action.payload
            //location存一份
            // localStorage.setItem("token_Key", action.payload)
                _setToken(action.payload)
        },
        setUserInfo(state,action){
            state.userInfo=action.payload
        },
        //退出登录
        clearUserInfo(state){
            state.token=""
            state.userInfo={}
            removeToken()
        }
    }
})

//解构出actionCreater
const { setToken,setUserInfo,clearUserInfo } = userStore.actions

//获取reducer函数
const userReducer = userStore.reducer

//异步方法 完成登录获取token
const fetchLogin = (loginForm) => {
    return async (dispatch) => {
        // 1发送异步请求
        // const res = await request.post("/authorizations", loginForm)   这个把请求放到这里不好管理
        const res = await LoginApi(loginForm)   //抽离出去后好管理
        //2提交同步action进行token的存入
        dispatch(setToken(res.data.token))
    }
}

//异步方法 获取用户信息
const fetchUserInfo = () => {
    return async (dispatch) => {
        // 1发送异步请求
        // const res = await request.get("/user/profile")
        const res = await getProfileApi()
        //2提交同步action进行token的存入
        dispatch(setUserInfo(res.data))
    }
}

export { fetchLogin,fetchUserInfo,clearUserInfo, setToken }

export default userReducer