import React from 'react';
import "./index.scss"
import { Card, Button, Checkbox, Form, Input, message } from 'antd';
import { useDispatch } from 'react-redux';
import { fetchLogin } from '../../store/modules/user';
import { useNavigate } from 'react-router-dom';



const Login = () => {
  const dispatch = useDispatch()
  const navigate=useNavigate()
  const onFinish =async (values) => {
    console.log('Success:', values);
    //触发异步action fetchLogin user.js里面的
   await dispatch(fetchLogin(values))
    //跳转首页
    navigate("/")
    //提示一下用户
    message.success("登录成功")
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };
  return (<div>

    <Card
      className='CardBox'
      hoverable
      style={{ width: 550, margin: "0 auto", position: 'absolute', top: "50%", left: "50%" }}
    >
      <Form
        validateTrigger="onBlur"
        name="basic"
        labelCol={{
          span: 8,
        }}
        wrapperCol={{
          span: 16,
        }}
        style={{
          maxWidth: 600,
        }}

        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
         
        >
          <h1 style={{ margin: "0 auto" }}>React</h1>
        </Form.Item>
        <Form.Item
          label="mobile"
          name="mobile"
          rules={[
            {
              required: true,
              message: 'Please input your mobile!',
            },
            {
              pattern: /^1[3-9]\d{9}$/,
              message: "place input true mobile"
            }
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="code"
          name="code"
          rules={[
            {
              required: true,
              message: 'Please input your code!',
            },
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          
          valuePropName="checked"
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
         
        </Form.Item>

        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Button type="primary" htmlType="submit">
            Login
          </Button>
        </Form.Item>
      </Form>
    </Card>
  </div>
  )
}

export default Login