// export default Layout

import React, { useEffect } from 'react';
import { LaptopOutlined, NotificationOutlined, UserOutlined } from '@ant-design/icons';
import { Layout, Menu, Popconfirm, theme, Button, message } from 'antd';
import { Outlet, useLocation, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { clearUserInfo, fetchUserInfo } from '../../store/modules/user';
import "./index.scss"

const { Header, Content, Footer, Sider } = Layout;
const items1 = ['1', '2', '3'].map((key) => ({
  key,
  label: `nav ${key}`,
}));
const title = ['首页', "文章管理", '创建文章']
const items2 = [{ title: UserOutlined, key: "/" }, { title: LaptopOutlined, key: "/article" }, { title: NotificationOutlined, key: "/publish" }].map((icon, index) => {
  const key = String(index + 1);
  return {
    key: `${icon.key}`,
    icon: React.createElement(icon.title),
    label: `${title[index]}`,
    // children: new Array(4).fill(null).map((_, j) => {
    //   const subKey = index * 4 + j + 1;
    //   return {
    //     key: subKey,
    //     label: `option${subKey}`,
    //   };
    // }),
  };
});
const LayOut = () => {
  const navigate = useNavigate()
  //方法定义
  // ---------------------------------------------------------------------
  const handleClick = (key) => {
    // console.log("点击了",key);
    navigate(key.key)
  }

  //反向高亮
  //1获取当前地址栏路径
  const url = useLocation()
  const { pathname } = url
  // console.log(pathname);

  //触发个人用户信息action
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(fetchUserInfo())
  }, [dispatch])
  const name = useSelector(state => state.user.userInfo.name)
  // console.log(useSelector(state=>state.user.userInfo.name));

  //退出
  const confirm = (e) => {
    // console.log(e);
    dispatch(clearUserInfo())
    message.success('退出成功');
    navigate("/login")
  };
  const cancel = (e) => {
    message.error('退出失败');
  };
  // ---------------------------------------------------------------------
  const {
    token: { colorBgContainer },
  } = theme.useToken();
  return (
    <Layout >
      <Header
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <div className="demo-logo" style={{ fontSize: "25px" }}>React</div>
        {/* <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['2']} items={items1} /> */}
        <span className='user-name'>{name}</span>
        <span className='user-Layout'>
          <Popconfirm title="确认退出吗?" okText="确认" cancelText="取消" onConfirm={confirm} onCancel={cancel}>
            {/* <LogoutOutlined/>退出 */}
            <Button danger>退出</Button>
          </Popconfirm>
        </span>
      </Header>
      <Content
        style={{
          padding: '0 50px',
          height:"100%"
        }}
      >
        {/* <Breadcrumb
          style={{
            margin: '16px 0',
          }}
        >
          <Breadcrumb.Item>Home</Breadcrumb.Item>
          <Breadcrumb.Item>List</Breadcrumb.Item>
          <Breadcrumb.Item>App</Breadcrumb.Item>
        </Breadcrumb> */}
        <Layout
          style={{
            padding: '24px 0',
            background: colorBgContainer,
          }}
        >
          <Sider
            style={{
              background: colorBgContainer,
            }}
            width={200}
          >
            <Menu
              mode="inline"
              // defaultSelectedKeys={['1']}
              selectedKeys={pathname}
              // defaultOpenKeys={['sub1']}
              style={{
                height: '100%',
              }}
              items={items2}
              onClick={handleClick}
            />
          </Sider>
          <Content
            style={{
              padding: '0 24px',
              minHeight: 280,
            }}
          >
            {/* Content区 */}
            <Outlet></Outlet>

          </Content>
        </Layout>
      </Content>
      {/* <Footer
        style={{
          textAlign: 'center',
        }}
      >
        Ant Design ©2023 Created by Ant UED
      </Footer> */}
    </Layout>
  );
};
export default LayOut